<template>
  <div class="naics-code">
    <el-select :placeholder="$t('select_business_sector')" class="form-control" :value="selectedSector" v-model="selectedSector" @change="handleSectorChange">
      <el-option v-for="item in data"
                 :key="item.code"
                 :label="item.name"
                 :value="item.code">
        <div class="option-grid">
          <div class="title" :title="item.name">
            {{ item.name }}
          </div>
          <div class="value">
            {{ item.code }}
          </div>
        </div>
      </el-option>
    </el-select>

    
    <el-select :placeholder="$t('select_industry_group')" class="form-control" :value="selectedSector" v-model="selectedIndustry" @change="handleIndustryChange">
      <template v-if="selectedSectorData">
        <el-option v-for="item in selectedSectorData.industries"
                   :key="item.code"
                   :label="item.name"
                   :value="item.code">
          <div class="option-grid">
            <div class="title" :title="item.name">
              {{ item.name }}
            </div>
            <div class="value">
              {{ item.code }}
            </div>
          </div>
        </el-option>
      </template>
    </el-select>

    <el-select :placeholder="$t('select_national_industry')" class="form-control" :value="selectedSector" v-model="selectedNationalIndustry" @change="handleNationalIndustryChange">
      <template v-if="selectedIndustryData">
        <el-option v-for="item in selectedIndustryData.nationalIndustries"
                   :key="item.code"
                   :label="item.name"
                   :value="item.code">
          <div class="option-grid">
            <div class="title" :title="item.name">
              {{ item.name }}
            </div>
            <div class="value">
              {{ item.code }}
            </div>
          </div>
        </el-option>
      </template>
    </el-select>
  </div>
</template>
<script>
export default {
  props: {
    // eslint-disable-next-line vue/require-default-prop
    data:  {
      type: Array,
    },
    // defaultValue: {
    //   type: String,
    //   default: ''
    // },
    value: {
      type: String,
      default: ''
    },
  },
  data() {
    return {
      selectedSector: null,
      selectedIndustry: null,
      selectedNationalIndustry: null,
    }
  },
  computed: {
    selectedSectorData() {
      if(this.selectedSector && this.data) {
        return this.data.find((s) => s.code === this.selectedSector)
      }
      return null
    },
    selectedIndustryData() {
      if(this.selectedIndustry && this.selectedSectorData) {
        return this.selectedSectorData.industries.find((s) => s.code === this.selectedIndustry)
      }
      return null
    }
  },
  methods: {
    updateValue() {
      console.log('this.value', this.value, this.data.length)
      if (this.data.length !== 0 && this.value && this.value != '') {
        const data = this.data;
        let selectedItem;
        console.log('this.value', this.value)
        // eslint-disable-next-line no-plusplus
        for (let i = 0; i < data.length; i++) {
        // eslint-disable-next-line no-plusplus
          for (let j = 0; j < data[i].industries.length; j++) {
            selectedItem = data[i].industries[j].nationalIndustries.find((item) => `${item.code}` === this.value);
            if (selectedItem) {
              this.selectedSector = data[i].code;
              this.selectedIndustry = data[i].industries[j].code;
              this.selectedNationalIndustry = selectedItem.code;
            }
          }
        }
      }
    },
    handleSectorChange(v) {
      this.selectedIndustry = null
      this.selectedNationalIndustry = null;
      this.$emit('change', v+'XXXX')
    },
    handleIndustryChange(v) {
      this.selectedNationalIndustry = null;
      this.$emit('change', v+'XX')
    },
    handleNationalIndustryChange(v) {
      this.$emit('change', ''+v)
    }
  },
  watch: {
    value() {
      this.updateValue()
    },
    data() {
      this.updateValue()
    },
  }
}
</script>
<style lang="scss" scoped>
.form-control {
    width: 100%;
    margin-bottom: 12px;
}
.option-grid {
    display: flex;
    justify-content: space-between;
    .title {
      max-width: 90%;
      overflow: hidden;
      text-overflow: ellipsis;
      padding-right: 10px;
    }
}

</style>
<style lang="scss">
.naics-code {
  .el-select-dropdown__item {
    height: unset;
  }
}
</style>